 * @LastEditors: YML
 * @LastEditTime: 2021-05-25 15:36:15
-->
<template>
  <div class="equipment-admin">
    <box>
      <!-- 信息筛选 start -->
      <div class="search">
        <div class="search-body">
          <a-form-model
            layout="inline"
            class="search-main"
            ref="searchForm"
            :model="searchForm"
          >
            <a-form-model-item label="设备名称：" prop="name">
              <a-input
                class="default-input"
                v-model="searchForm.name"
                placeholder="请输入设备名称"
                allow-clear
              />
            </a-form-model-item>
            <a-form-model-item label="设备状态：" prop="type">
              <a-select
                class="default-select"
                dropdown-class-name="default-select-dropdown"
                v-model="searchForm.type"
                placeholder="请选择设备状态"
                :getPopupContainer="triggerNode => triggerNode.parentNode"
              >
                <a-select-option value="1">
                  正常
                </a-select-option>
                <a-select-option value="2">
                  异常
                </a-select-option>
              </a-select>
            </a-form-model-item>
            <a-form-model-item label="上报状态：" prop="appearType">
              <a-select
                class="default-select"
                dropdown-class-name="default-select-dropdown"
                v-model="searchForm.appearType"
                placeholder="请选择上报状态"
                :getPopupContainer="triggerNode => triggerNode.parentNode"
              >
                <a-select-option value="1">
                  已上报
                </a-select-option>
                <a-select-option value="2">
                  未上报
                </a-select-option>
              </a-select>
            </a-form-model-item>
            <a-form-model-item label="开始时间：" prop="startTime">
              <a-date-picker
                class="default-date-picker"
                show-time
                v-model="searchForm.startTime"
                dropdown-class-name="default-date-picker-dropdown"
                :get-calendar-container="triggerNode => triggerNode.parentNode"
              />
            </a-form-model-item>
            <a-form-model-item label="结束时间：" prop="endTime">
              <a-date-picker
                class="default-date-picker"
                show-time
                v-model="searchForm.endTime"
                dropdown-class-name="default-date-picker-dropdown"
                :get-calendar-container="triggerNode => triggerNode.parentNode"
              />
            </a-form-model-item>
          </a-form-model>
        </div>
      </div>
      <!-- 信息筛选 end -->
      <!-- 编辑导航 start -->
      <div class="operation-nav">
        <div class="btn-box">
          <a-button class="operation-button">
            <span class="iconfont iconfont-14 icon-sousuo1"></span>
            <span class="btn-title">查询</span>
          </a-button>
          <a-button class="operation-button" @click="toReset">
            <span class="iconfont iconfont-18 icon-zhongzhi"></span>
            <span class="btn-title">重置</span>
          </a-button>
          <a-button class="operation-button" @click="addMessage">
            <span class="iconfont iconfont-16 icon-bianji2"></span>
            <span class="btn-title">新增</span>
          </a-button>
          <a-button class="operation-button">
            <span class="iconfont iconfont-14 icon-shanchu"></span>
            <span class="btn-title">批量停用</span>
          </a-button>
        </div>
      </div>
      <!-- 编辑导航 end -->
      <!-- 表格展示 start -->
      <div class="contents">
        <a-table
          class="default-table"
          :columns="columns"
          :data-source="data"
          :pagination="false"
          :row-selection="rowSelection"
          :scroll="{ y: 'calc(90vh - 18.5vw)' }"
        >
          <div slot="type" slot-scope="text, record">
            <div class="enabled" v-if="record.type">
              <div class="spot"></div>
              <div class="type-name">正常</div>
            </div>
            <div class="no-enabled" v-else>
              <div class="spot"></div>
              <div class="type-name">异常</div>
            </div>
          </div>
          <div slot="appearType" slot-scope="text, record">
            <div v-if="record.appearType">
              已上报
            </div>
            <div class="no-sign" v-else>
              未上报
            </div>
          </div>
          <div slot="operation" class="operation">
            <div class="count" @click="editMessage()">
              <span class="iconfont iconfont-18 icon-bianji"></span>
              <span class="title">编辑</span>
            </div>
            <div class="count" @click="getMessage()">
              <span class="iconfont iconfont-18 icon-yanjing"></span>
              <span class="title">查看</span>
            </div>
            <div class="count" @click="deleteMessage()">
              <span class="iconfont iconfont-16 icon-del"></span>
              <span class="title">删除</span>
            </div>
          </div>
        </a-table>
      </div>
      <!-- 表格展示 end -->
      <!-- 分页 start -->
      <div class="content-pagination">
        <pagination
          ref="pagination"
          :total="100"
          @change="onPaginationChange"
        />
      </div>
      <!-- 分页 end -->
    </box>
    <!-- 新增弹窗 start -->
    <editmodal
      :title="'新增信息'"
      :visible="addModal"
      @close="closeModal"
    ></editmodal>
    <!-- 新增弹窗 end -->
    <!-- 编辑弹窗 start -->
    <editmodal
      :title="'编辑信息'"
      :visible="editModal"
      @close="closeModal"
    ></editmodal>
    <!-- 编辑弹窗 end -->
    <!-- 查看弹窗 start -->
    <editmodal
      :title="'查看详情'"
      :visible="examineModal"
      @close="closeModal"
    ></editmodal>
    <!-- 查看弹窗 end -->
    <!-- 删除弹窗 start -->
    <deletemodal :visible="deleteModal" @close="closeModal"></deletemodal>
    <!-- 删除弹窗 end -->
  </div>
</template>

<script>
import Box from "../../components/Box";
import Pagination from "../../components/Pagination";
import editmodal from "./editModal";
import deletemodal from "../../components/deleteModal";
export default {
  components: {
    Box, //边框组件
    Pagination, //分页组件
    editmodal, //新增、编辑、查看弹窗
    deletemodal //删除弹框
  },
  data() {
    return {
      pageSize: 10, // 每页条数
      page: 1, // 当前页
      searchForm: {
        name: "", //设备名称
        type: undefined, //设备状态
        appearType: undefined, //上报状态
        startTime: null, //开始时间
        endTime: null //结束时间
      },
      columns: [
        {
          title: "设备名称",
          dataIndex: "name",
          key: "name",
          width: "9vw"
        },
        {
          title: "设备标识",
          dataIndex: "identification",
          key: "identification",
          width: "9vw"
        },
        {
          title: "品牌型号",
          dataIndex: "model",
          key: "model",
          width: "9vw"
        },
        {
          title: "设备状态",
          dataIndex: "type",
          key: "type",
          width: "9vw",
          scopedSlots: { customRender: "type" }
        },
        {
          title: "上报状态",
          dataIndex: "appearType",
          key: "appearType",
          width: "9vw",
          scopedSlots: { customRender: "appearType" }
        },
        {
          title: "上传时间",
          dataIndex: "upTime",
          key: "upTime",
          width: "12vw"
        },
        {
          title: "操作",
          dataIndex: "operation",
          key: "operation",
          width: "12vw",
          scopedSlots: { customRender: "operation" }
        }
      ],
      data: [],
      selectedRowKeys: [], // 列表项是否可选择
      addModal: false, //新增操作弹窗是否显示
      editModal: false, //编辑操作弹窗是否显示
      examineModal: false, //查看操作弹窗是否显示
      deleteModal: false //删除操作弹窗是否显示
    };
  },
  computed: {
    rowSelection() {
      const { selectedRowKeys } = this;
      return {
        selectedRowKeys,
        onChange: this.onSelectChange,
        hideDefaultSelections: true
      };
    }
  },
  mounted() {
    //设置分页数据
    this.$refs.pagination.setData(this.page, this.pageSize);
    // 加载表格数据
    this.setList();
  },
  methods: {
    /**
     * 向列表项追加内容
     */
    setList() {
      let data = [];
      for (let i = 0; i < this.pageSize; i++) {
        let item = {
          key: i,
          name: `设备${i}`,
          identification: "15uegru",
          model: "A-03EU8",
          type: i % 2 == 1 ? true : false,
          appearType: i % 2 == 1 ? true : false,
          upTime: "2020-01-02 12:00:30"
        };
        data.push(item);
      }
      this.data = data;
    },
    /**
     * 点击重置按钮
     */
    toReset() {
      this.$refs.searchForm.resetFields();
    },
    // 选中状态
    onSelectChange(selectedRowKeys) {
      console.log("selectedRowKeys changed: ", selectedRowKeys);
      this.selectedRowKeys = selectedRowKeys;
    },
    /**
     * 点击×号关闭弹框
     */
    closeModal() {
      this.addModal = false;
      this.editModal = false;
      this.examineModal = false;
      this.deleteModal = false;
    },
    /**
     * 点击新增打开弹窗
     */
    addMessage() {
      this.addModal = true;
    },
    /**
     * 点击编辑打开弹窗
     */
    editMessage() {
      this.editModal = true;
    },
    /**
     * 点击查看打开弹窗
     */
    getMessage() {
      this.examineModal = true;
    },
    /**
     * 点击删除打开弹窗
     */
    deleteMessage() {
      this.deleteModal = true;
    },
    /**
     * 分页事件
     */
    onPaginationChange(page, pageSize) {
      console.log(page, pageSize);
      this.pageSize = pageSize;
      this.setList();
    }
  }
};
</script>

<style lang="less">
@import "./index.less";
</style>
