<!--
 * @Description: 编辑弹窗
 * @Autor: YML
 * @Date: 2021-05-14 11:53:32
 * @LastEditors: YML
 * @LastEditTime: 2021-05-20 16:03:29
-->
<template>
  <div class="edit-modal">
    <modal
      width="44.58vw"
      height="30.78vw"
      :title="title"
      @close="$emit('close')"
      :visible="visible"
    >
      <img
        slot="modalBg"
        class="img-edit"
        src="../../assets/img/components/img-edit2.png"
        alt=""
      />
      <a-form-model
        layout="inline"
        class="form-edit"
        ref="ruleForm"
        :model="form"
        :rules="rules"
      >
        <a-form-model-item label="设备名称：" prop="name">
          <a-input
            class="default-input"
            placeholder="请输入设备名称"
            :disabled="isShow"
          />
        </a-form-model-item>
        <a-form-model-item label="设备状态：" prop="type">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择设备状态"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              正常
            </a-select-option>
            <a-select-option value="2">
              异常
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="设备标识：" prop="signboard">
          <a-input
            class="default-input"
            placeholder="请输入设备标识"
            :disabled="isShow"
          />
        </a-form-model-item>
        <a-form-model-item label="品牌型号：">
          <a-input
            class="default-input"
            placeholder="请输入品牌型号"
            :disabled="isShow"
          />
        </a-form-model-item>
        <a-form-model-item label="网络端口号：">
          <a-input
            class="default-input"
            placeholder="请输入网络端口号"
            :disabled="isShow"
          />
        </a-form-model-item>
        <a-form-model-item label="IP地址：">
          <a-input
            class="default-input"
            placeholder="请输入IP地址"
            :disabled="isShow"
          />
        </a-form-model-item>
        <a-form-model-item label="备注">
          <a-textarea
            class="default-textarea"
            :disabled="isShow"
            placeholder="请填写"
          />
        </a-form-model-item>
      </a-form-model>
      <div class="line" v-show="!isShow"></div>
      <!-- 取消和保存按钮 start -->
      <div class="operation-btn" v-show="!isShow">
        <a-button type="default" class="form-button" @click="$emit('close')"
          >取消</a-button
        >
        <a-button type="primary" class="form-button save-button" @click="save">保存</a-button>
      </div>
      <!-- 取消和保存按钮 end -->
    </modal>
  </div>
</template>

<script>
import Modal from "../../components/Modal";
export default {
  components: {
    Modal //弹窗组件
  },
  props: {
    // 对话框是否可见
    visible: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      form: {
        name: "",
        type: "",
        signboard: ""
      },
      rules: {
        name: [
          {
            required: true,
            message: "请输入设备名称！",
            trigger: "blur"
          }
        ],
        type: [
          {
            required: true,
            message: "请选择设备状态！",
            trigger: "blur"
          }
        ],
        signboard: [
          {
            required: true,
            message: "请输入设备标识！",
            trigger: "blur"
          }
        ]
      },
      isShow: this.title == "查看详情"
    };
  },
  methods: {
    save() {
      console.log(this.form);
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>

<style lang="less"></style>
